@import url(https://fonts.googleapis.com/css?family=Roboto:400,700&display=swap);
main form[name="topBoxForm"] {
    margin-top: 30px;
    text-align: left;
}

main form[name="topBoxForm"] .inputBlock {
    margin-top: 13px;
    display: grid;
    grid-gap: 20px;
    gap: 20px;
    grid-template-columns: 1fr 3fr;
}

main form[name="topBoxForm"] .inputBlock label {
    color: #ACACAC;
    font-size: 14px;
    font-weight: bold;
    display: block;
}

main form[name="topBoxForm"] .inputBlock input {
    width: 100%;
    height: 20px;
    color: #666;
    font-size: 14px;
    border-top: 1px solid #eee;
    border-left: 1px solid #eee;
    border-bottom: 1px solid #f9f9f9;
    border-right: 1px solid #f9f9f9;
}

main form[name="topBoxForm"] .inputBlock textarea {
    width: 100%;
    height: 140px;
    color: #666;
    font-size: 16px;
    border-top: 1px solid #eee;
    border-left: 1px solid #eee;
    border-bottom: 1px solid #f9f9f9;
    border-right: 1px solid #f9f9f9;
    resize: none;
}

main form[name="topBoxForm"] button[type=submit] {
    width: 100%;
    border: 0;
    margin-top: 30px;
    background: #006699;
    border-radius: 2px;
    padding: 15px 20px;
    font-size: 16px;
    font-weight: bold;
    color: #FFF;
    cursor: pointer;
    -webkit-transition: background 0.5s;
    transition: background 0.5s;
}

main form[name="topBoxForm"] button[type=submit]:hover {
    background-color: #358eb7;
}

main form[name="topBoxForm"] button[type=submit]:active {
    background-color: #9fb8c4;
    -webkit-transition: background 0.2s;
    transition: background 0.2s;
}
li.content-block {
    position: relative;
    margin-top: 30px; 
    margin-left: 30px;
    width: 300px;
    background: #FFF;
    border-radius: 10px;
    padding: 15px 20px;
    text-align: center;
    display: inline-block;
    flex-direction: row;
    align-items: center;
    -webkit-transition: background 1s;
    transition: background 1s;
    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */ /* Konqueror HTML */
       -moz-user-select: none; /* Old versions of Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently supported by Chrome, Opera and Firefox */
}

li div.edit-delete-bar {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    -webkit-transition: opacity 0.7s;
    transition: opacity 0.7s;
    display: flex;
    align-items: center;
    border-radius: 10px;
    background: rgba(79, 113, 145);
}

li div.edit-delete-bar:hover {
    opacity: 1;
    -webkit-transition: opacity 0.7s;
    transition: opacity 0.7s;
    display: flex;
    align-items: center;
    background: rgba(79, 113, 145, 0.931);
}

li div.editDiv {
    margin-left: 90px;
    float:left;
}

li div.deleteDiv {
    float:left;
    margin-left: 50px;
}

.iconHover {
    color: #FFF;
    font-size: 40px;
    opacity: 1;
    -webkit-transition: opacity 0.5s ease;
    transition: opacity 0.5s ease;
}

.iconHover:hover {
    opacity: 0.4;
    -webkit-transition: opacity 0.5s ease;
    transition: opacity 0.5s ease;
}

li.content-block p {
    text-align: justify;
}

li.content-block span {
    color: #006699;
    text-align: right;
    display: flex;
    flex-direction: column;
    padding: 15px 0px 0px;
    font-size: 14px;
    font-weight: 500;
}

li span[name="spanDate"] {
    float:right;
}

li span[name="spanAuthor"] {
    float:left;
}
*{
    margin: 0;
    outline: 0;
    padding: 0;
    box-sizing: border-box;
}

html, body, #root {
    height: 100%;
}

body{
    background: #E5E6F0;
    -webkit-font-smoothing: antialised;
}

body, input, button, label {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif
}
#app {
  max-width: 1400px;
  margin: 0 auto;
  padding: 30px 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

@media (max-width: 800px) {
  #app {
      flex-direction: column;
  }

  #app main {
      width: 100%;
  }
}
main .topBox {
    margin-top: 30px; 
    width: 400px;
    background: #FFF;
    border-radius: 10px;
    padding: 15px 20px;
    text-align: center;
}

main strong {
    font-size: 18px;
    text-align: center;
    display: block;
    color: #333;
}

div .content {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
}


